const ScrollProgressBar = () => {
  const { scrollYProgress } = useScroll();
  const scaleX = useSpring(scrollYProgress, {
    stiffness: 100,
    damping: 30,
    restDelta: 0.001,
  });

  return (
    <motion.div
      className="fixed top-0 left-0 right-0 h-1 bg-cyan-400 origin-left z-[60]"
      style={{ scaleX }}
    />
  );
};


// MAIN APP COMPONENT
export default function App() {
  const [selectedProject, setSelectedProject] = useState(null);
  const [navOpen, setNavOpen] = useState(false);

  useEffect(() => {
    if (selectedProject || navOpen) {
      document.body.style.overflow = "hidden";
    } else {
      document.body.style.overflow = "auto";
    }
  }, [selectedProject, navOpen]);

  return (
    <div className="bg-slate-900 text-white font-sans leading-relaxed selection:bg-cyan-400 selection:text-slate-900 overflow-x-hidden">
      <ScrollProgressBar />
      <div className="fixed top-0 left-0 w-full h-full z-0">
        <Canvas camera={{ position: [0, 0, 1] }}>
          <Suspense fallback={null}>
            <Stars />
          </Suspense>
          <Preload all />
        </Canvas>
      </div>

      <div className="relative z-10">
        <Header setNavOpen={setNavOpen} />
        <MobileNav navOpen={navOpen} setNavOpen={setNavOpen} />
        <main>
          <Hero />
          <About />
          <Experience />
          <Projects setSelectedProject={setSelectedProject} />
          <Skills />
          <Contact />
        </main>
        <Footer />
      </div>

      <ProjectModal project={selectedProject} setSelectedProject={setSelectedProject} />
    </div>
  );
}
